import Link from 'next/link';
import Image from 'next/image';
import { useState } from 'react';

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="fixed top-0 left-0 w-full bg-gray-800 text-white shadow-md z-50">
      <div className="flex justify-between items-center p-4">
        {/* Logo Section */}
        <div className="flex items-center space-x-2">
          <Image src="/logo.jpg" alt="Logo" width={120} height={40} />
          <span className="text-lg font-bold">My Project</span>
        </div>

        {/* Hamburger Menu (Mobile) */}
        <button
          className="block md:hidden"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          <span className="text-2xl">&#9776;</span>
        </button>

        {/* Navigation Links (Desktop) */}
        <div className="hidden md:flex space-x-4">
          <Link href="/" className="hover:underline">
            Home
          </Link>
          <Link href="/menu" className="hover:underline">
            Menu
          </Link>
          <Link href="/entreprise-forfait" className="hover:underline">
            Entreprise Forfait
          </Link>
        </div>
      </div>

      {/* Mobile Menu */}
      {isMenuOpen && (
        <div className="md:hidden flex flex-col space-y-2 p-4 bg-gray-700">
          <Link href="/" className="hover:underline" onClick={() => setIsMenuOpen(false)}>
            Home
          </Link>
          <Link href="/menu" className="hover:underline" onClick={() => setIsMenuOpen(false)}>
            Menu
          </Link>
          <Link href="/entreprise-forfait" className="hover:underline" onClick={() => setIsMenuOpen(false)}>
            Entreprise Forfait
          </Link>
        </div>
      )}
    </nav>
  );
};

export default Navbar;